.group {
    position: relative;
    padding: 5px;
    padding-top: 20px;
    border: 1px dashed #646464;
    border-radius: 8px;
    margin-top: 30px;
    transition: border-color .1s;

    &:hover{
        border-color: rgba(204, 204, 204, 0.8);
    }

    h3{
        display: block;
        margin: 0;
        position: absolute;
        color: darkcyan;
        font-size: .8rem;
        top: -8px;
        left: 20px;
        padding: 0 6px 0 6px;
        background-color: #303030;
    }

    &.object{
        h3{
            color: rgb(121, 199, 199);
        }
    }

}